.masonry{
    -webkit-transition-duration: 1s;
            transition-duration: 1s;
    z-index: -1;
    margin: 50px auto;
    width: 65%;
}

@media only screen and (min-width: 1024px) {
    .masonry {
        -webkit-column-count: 4;
                column-count: 4;
    }
}

@media only screen and (max-width: 1023px) and (min-width: 768px) {
    .masonry {
        -webkit-column-count: 3;
                column-count: 3;
    }
}

.item {
    background: none;
    display: inline-block;
    margin: 0 0 1em;
    width: 100%;
    margin: 16px;
}

.item:hover {
    cursor: pointer;
}

.pagination {
    font-family: "Open Sans";
    font-weight: bolder;
    font-style: italic;
    position: relative;
    background: lightgray;
    border-radius: 10px;
    display: inline-block;
    left: 330px;
    top: 45px;
    margin: 50px auto ;
    list-style: none;
}

.pagination > * {
    float: left;
    color: #050417;
    padding: 8px 16px;
    text-decoration: none;
    -webkit-transition: background-color .3s;
    transition: background-color .3s;
}

.pagination > * > a {
    color: #050417;
    text-decoration: none;
}

.pagination > *.active > a {
    cursor: default;
    color: lightblue;
    text-decoration: none;
}

.pagination > *.active {
    background-color: #050417;
    color: white;
    border-radius: 5px;
}

.pagination > *:hover:not(.active) {background-color: rosybrown; border-radius: 5px; color: white; cursor: pointer}

.midModal {width: 100%; margin-top: 20px;}

